import React, { Component } from 'react';
import './App.css';
import CircleSelector from './CircleSelector/CircleSelector';
import Circles from './Circles/Circles';

class App extends Component {
  state = {
    numbers: [1, 2, 3, 4],
    selectedNumber: 1
  }

  handleClick = (clickedNumber) => {
    this.setState({
      selectedNumber: clickedNumber
    })
  }

  render() {
    const { numbers, selectedNumber } = this.state;
    return (
      <div className="App">
        <header className="App-header">Bryant Cabrera's PROJECT 3 ASSESSMENT</header>
        <main>
          <CircleSelector numbers={numbers} selectedNumber={selectedNumber} handleClick={this.handleClick} />
          <Circles numbers={numbers} selectedNumber={selectedNumber} />
        </main>
      </div>
    );
  }
}

export default App;