import React from 'react';
import './CircleSelector.css';

const CircleSelector = ({ numbers, selectedNumber, handleClick }) => {
    return (
        <div className="CircleSelector">
            { numbers.map((number, index) => <button key={index} onClick={() => handleClick(number)} className={selectedNumber === number ? 'selected' : ''}>{selectedNumber === number ? `CIRCLE ${number} SELECTED` : `SELECT CIRCLE ${number}`}</button>)}
        </div>
    )
};

export default CircleSelector;